import React, { Component } from 'react';
import api from '../../../Global/Api';
import {Row, Col, Card} from 'react-bootstrap';
import Aux from "../../../../hoc/_Aux";
import {Modal} from 'antd';

class App extends Component {

  state = {
    activityid:  this.props.match.params.ActivityId,
    image: null,
    activityimg: [],
  };

  componentDidMount (){
    this.fetchimage();
  }

  success() {
    Modal.success({
      content: 'Successively Updated',
    });
  }
  
  error() {
    Modal.error({
      title: 'This is an error message',
      content: 'Error Occurred!!',
    });
  }

  handleChange = (e) => {
    this.setState({
      [e.target.id]: e.target.value
    })
  };

  handleImageChange = (e) => {
    this.setState({
      image: e.target.files[0]
    })
  };

  fetchimage = () => {
    this.setState({ loading: true });
    api.get(`/api/activityimglist?activity=${this.state.activityid}`,{})
    .then(res => {
        this.setState({
          loading: false,
          activityimg: res.data,
        });
      });
  };


  handleSubmit = (e) => {
    e.preventDefault();
    let form_data = new FormData();
    form_data.append('activity_img', this.state.image, this.state.image.name);
    form_data.append('activity', this.state.activityid);
    api.post(`/api/activityimg`, form_data, {
        headers: { 'Content-Type': 'multipart/form-data' }})
        .then(response => {
          if(response.status === 201){
            this.success();
            this.fetchimage();
          }
        })
        .catch(error => {
          this.error();
        });
  };
  

  render() {
    const activityimgvalue = this.state.activityimg.map((image) =>
       <Col md={6}><img alt={image.id} className="img-fluid p-3" src={image.activity_img} ></img> </Col>
      );
    return (
      <Aux>
                <Row>
                    <Col>
                        <Card>
                            <Card.Header>
                                <Card.Title as="h5">Image Upload</Card.Title>
                            </Card.Header>
                            <Card.Body>
                                <Row>
                                    <Col md={6}>
                                      <form onSubmit={this.handleSubmit}>
                                        <p>
                                          <input type="file"
                                                id="image"
                                                accept="image/png, image/jpeg"  onChange={this.handleImageChange} required/>
                                        </p>
                                        <input className="btn btn-primary" type="submit"/>
                                      </form>
                                    </Col>
                                    <Col md={6}>
                                      <Row>
                                        {activityimgvalue}
                                      </Row>
                                    </Col>
                                </Row>
                            </Card.Body>
                        </Card>
                    </Col>
                </Row>
            </Aux>
    );
  }
}

export default App;